import React from 'react';
import { Table } from 'react-bootstrap';
import '../../../shared/CSS/FormStyles.css';
import './Polls.css';
import { Button } from 'reactstrap';

const Polls = () => {
    return(
       
        <div className="jumbotron jumbo-form">
            <h5 className="page-heading">Polls</h5>
            <hr className="hr-line" />
            <div className="form-container">
                <form>
                    <div class = "table-responsive" >
                        <Table class="table" striped bordered hover>
                            <thead  className="border">   
                                <tr>
                                    <th>Poll Title</th>
                                    <th>Initiated by</th>
                                    <th>Priority</th>
                                    <th>Status</th>
                                    <th className="columnwidth"></th>  
                                </tr>
                            </thead>
                        
                            <tbody>
                                <tr>
                                    <td>Food Corner in campus</td>
                                    <td>John</td>
                                    <td>High</td>
                                    <td>Pending</td>
                                    <td className="btn-align">
                                         <Button outline color="secondary" >View Result</Button>
                                         <Button outline color="secondary" >Give Vote</Button>
                                    </td>
                                </tr>
                            </tbody>
                        </Table>
                    </div>
                </form>
            </div>
        </div>
    );
}

export default Polls;